@import "./_import.scss";

aside {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    gap: var(--spacing-small);

    section.filters {
        flex: 1;
        h4 {
            font-size: 0.9rem;
        }
    }

    ul {
        flex: 12;
        background-image: url("../images/grid-background.png");
        mask-image: linear-gradient(to bottom, transparent 0%, white 13% 87%, transparent 100%);
        overflow-x: hidden;
        overflow-y: auto;
        width: 100%;

        li {
            font-size: 0.75rem;
            transition: font-weight 0.2s;
            padding: 1px 6px;
            color: #c4c2c2;
            &:first-child,
            &:last-child {
                height: 50%;
            }

            &.active {
                font-weight: 800;
                color: #ffffff;
            }
        }
    }

    p.graph-label {
        position: absolute;
        bottom: var(--spacing);
        right: var(--spacing);
        writing-mode: horizontal-tb;
        font-style: italic;
        text-align: end;
        font-size: 0.7rem;
    }

    button,
    ul {
        pointer-events: all;
    }
}
